.prodcon{
    width:100%;
    display: flex;
    justify-content: space-evenly;
    gap:20px;
    flex-wrap: wrap;
  }
  .card{
    margin-top: 10px;
    width:30%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    justify-content: space-around;
    background-color:bisque ;
    padding-top: 4px;
    padding-bottom: 4px;
    
  }
  .pimg{
    width:100px;
    height: 100px;
    border-radius: 10px;
  
  }
  .pimg img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    
  }
  .card p,.card div{
    text-align: center;
  }
  @media screen and (max-width:768px) {
    .card{
      width:45%;
    }
    
  }
  *{
    outline: none;
    margin: 0;
    padding: 0;
  }